.wrapper_table {
  width: 100%;

  font-family: sans-serif;
  font-size: 14px;
}

.wrapper_table table {
  width: 99%;

  margin: 0 auto;

  border-collapse: collapse;
  font-family: Tahoma, Geneva, sans-serif;
  overflow: scroll;
}
.wrapper_table table td {
  padding: 5px;
  overflow-wrap: anywhere;
}
.wrapper_table table thead th {
  background-color: #54585d;
  color: #ffffff;
  font-weight: bold;

  border: 1px solid #fff;
}
.wrapper_table table thead div:first-child {
  margin: 5px;
}
.wrapper_table table tbody td {
  text-align: center;
  color: #636363;
  border: 1px solid #dddfe1;
}
.wrapper_table table tbody tr {
  background-color: #e7e7e7;
}
.wrapper_table table tbody tr:nth-child(odd) {
  background-color: #ffffff;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}
.amount_page {
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrapper_table input {
  width: 40px;
  margin: 5px 0;
}
.wrapper_table input[type="date"] {
  width: 100px;
}
.wrapper_filter input:nth-child(2) {
  margin-left: 10px;
}
.table-icon {
  cursor: pointer;
  margin: auto auto;
  width: 13px;
  height: 13px;
}
.table-icon.false {
  cursor: default;
}
.table_button_wrapper {
  width: 99%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  background-color: #54585d;
  border-collapse: collapse;
}
.table_button_wrapper button,
.table_button_wrapper select {
  padding: 10px;
  color: black;
  background-color: #fff;
  border: 1px solid #dddfe1;
  cursor: pointer;
}
/* form  */
.none_data {
  width: 150px;
  font-size: 30px;
  text-align: center;
  position: absolute;
  left: calc(50% - 75px);
  top: 50%;
}
.form_put {
  display: flex;
  flex-wrap: wrap;
}
.put_input_wrapper {
  justify-content: space-around;
  align-items: center;
  height: 45px;
  width: 50%;
  font-size: 16px;
}

.put_input_wrapper span {
  width: 15%;
}
.put_input_wrapper input {
  margin-top: 5px;
  width: 35%;
  height: 25px;
}
.modal_del_content {
  width: fit-content;
  height: fit-content;
  margin: auto auto;
}
.mobile_date {
  display: none;
}
@media only screen and (max-width: 600px) {
  .wrapper_table {
    min-width: 0;
    font-size: 10px;
  }
  .table_button_wrapper {
    width: 100%;
  }
  /* .wrapper_table table {
    width: 100%;
    display: block;
    overflow-x: scroll;
  } */

  .mobile_date {
    display: block;
  }
  .wrapper_filter input:nth-child(2) {
    margin-left: 0;
  }
  .form_put {
    display: block;
  }
  .form_put span {
    font-size: 13px;
  }
  .put_input_wrapper {
    width: 100%;
  }
}
