@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.2.0/css/font-awesome.min.css");
.head {
  font-family: "Open Sans", "sans-serif", "FontAwesome";
  color: #000;
  margin: 20px auto 0;
  width: 200px;
  text-align: center;
}
.wrapper_signin {
  box-sizing: border-box;
  width: 100vw;
  /* height: 100vh; */
  display: flex;
  flex-direction: column;
  padding-top: 30vh;
  align-items: center;
  /* background: #fff; */
  position: relative;
}

/* === Sign in Form === */
.signin {
  height: 90px;
  width: 300px;
  border-radius: 8px;
  position: relative;
}
.signin::before {
  display: block;
  position: relative;
  height: 2px;
  background: rgb(52, 56, 61);
  content: "";
  top: 44px;
  margin-left: 20px;
  z-index: 1;
}
.signin input:first-of-type {
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
}
.signin input:last-of-type {
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
}

.signin input[type="text"],
.signin input[type="password"],
.signin button[type="submit"] {
  background: rgb(28, 30, 33);
  box-shadow: inset -100px -100px 0 rgb(28, 30, 33); /*Prevent yellow autofill color*/
  color: #fff;
}
.signin input[type="text"],
.signin input[type="password"] {
  position: relative;
  display: block;
  width: 280px;
  height: 45px;
  border: 0;
  outline: 0;
  top: -2px;
  padding: 0 0 0 20px;
  font-weight: 700;
}

.signin input[type="text"]:focus,
.signin input[type="password"]:focus {
  color: rgb(255, 255, 255);
}

.signin button[type="submit"] {
  font-family: "Open Sans", "sans-serif", "FontAwesome";
  display: block;
  position: absolute;
  width: 52px;
  height: 52px;
  color: rgb(52, 56, 61);
  border-radius: 50px;
  outline: 0;
  z-index: 2;
  top: 19px;
  right: -24px;
  border: 6px solid #e5e5e5;
  font-size: 25px;
  text-indent: 0px;
  padding-left: 9px;
  padding-bottom: 3px;
  text-align: center;
}
.signin button[type="submit"]:hover {
  color: rgb(0, 126, 165);
  text-shadow: 0 0 10px rgb(0, 126, 165);
  cursor: pointer;
}

.signin p {
  color: rgb(79, 85, 97);
  padding: 0 20px;
  font-weight: 700;
  font-size: 12px;
  margin: 5px 0 0 0;
}
.signin p > a {
  color: rgb(111, 119, 135);
  text-decoration: none;
}
.signin p > a:hover {
  border-bottom: 1px solid;
}

.error {
  margin: 8px;
  color: #e53e3e;
}

.signin :-webkit-autofill {
  -webkit-text-fill-color: rgb(113 147 190);
}
